@font-face {
  font-family: 'GothamRnd-Medium';
  src: url('fonts/gothamrounded-medium-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Book';
  src: url('fonts/gothamrounded-book-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Bold';
  src: url('fonts/gothamrounded-bold-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'HelveticaNeue';
  src: url('fonts/HelveticaNeueCyr-Roman.woff') format('woff');
  font-weight: bold;
}

/* ============ FONT COLORS BEGIN ============== */
html {
  --par-font-color: #444444;
  --tag-font-color: #F7F7F7;
  --light-gray-font-color: #938F8F; /* not selected metatags, menu options etc. */
  --dark-gray-font-color: #555555; /* selected metatags, link hovers, column title etc. */
  --card-title-font-color: #4E4E4E; /* quiz card title */
}
/* ============ FONT COLORS END ============== */

.everything {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 400%;
}

.sticky-header {
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  height: 58px;
  background: #FFFFFF;
  z-index: 10;
  /*box-shadow: 0 -4px 14px 3px rgba(0,0,0,0.02);*/
  border-bottom: 1px solid black;
}

.intact-logo {
  text-align: center;
  font-family: 'GothamRnd-Bold';
  text-transform: uppercase;
  font-size: 124px;
  color: rgb(247, 241, 234);
  letter-spacing: 0.18px;
  margin: auto;
  z-index: 100;
}

.parallax-container {
  margin-top: -58px;
  height: 400vh;
  overflow: hidden;
  top: -58px;
}

.landing-slide {
  top: -58px;
  z-index: 20;
  background: rgb(247, 241, 234);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.topbar-spotlight-wrapper {
    position: absolute;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    z-index: 2;
}

.topbar-spotlight {
    z-index: 2;
    height: auto;
    position: absolute;
    overflow: hidden;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: block;
    pointer-events: auto;
}

.landing-slide, .first-slide, .second-slide, .third-slide, .fourth-slide {
  position: relative;
  height: 100vh;
  width: 100vw;
  /*padding-top: 58px;*/
}

.processing {
  /*background: #ddd4c1;*/
  height: 100vh;
  width: 100vw;
  background: rgb(247, 241, 234);
  /*z-index: 20;*/
}

.App-logo, .logo {
  text-align: center;
  font-family: 'GothamRnd-Bold';
  text-transform: uppercase;
  font-size: 20px;
  color: rgb(247, 241, 234);
  letter-spacing: 0.18px;
  margin: auto;
}

.logo {
  color: var(--dark-gray-font-color);
}

.aligner {
  margin-top: 58px;
  position: absolute;
  width: 100%;
  height: calc(100% - 58px);
}

.aligned {
  margin: auto;
}

.flex-grow {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.par {
  /*text-align: justify;*/
  float: left;
  font-family: 'HelveticaNeue';
  font-size: 38px;
  color: black;
  letter-spacing: 0;
  line-height: 48px;
  padding: 38px;
}

.aligner-box {
  height: 100%;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.central-par {
  padding-top: 8%;
}

.second-page-par, .central-par {
  max-width: 100%;
  padding: 12px;
  font-size: 20px;
}

.role {
  padding: 12px;
}

@media only screen and (min-width: 40em) {
  .top-par {
    padding-top: 28%;
  }

  .top-par-second {
    padding-top: 24%;
  }
}

.down-arrow {
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-left: 50%;
  /*padding-top: 16%;*/
  width: 8%;
}

.down-arrow-wrapper {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.roles {
  min-width: 100%;
}

.role, .role-card {
  border-radius: 5px;
  padding: 12px;
  font-family: 'HelveticaNeue';
  vertical-align: 'center';
  text-align: center;
  color: #787676;
  letter-spacing: -0.14px;
}

.role, .role-card .role-title {
  text-transform: uppercase;
  border: 1px solid var(--tag-font-color);
  font-size: 20px;
  background: #FFFFFF;
  min-width: 25%;
}

.role-card .role-description {
  text-transform: none;
}

.role-card .role-value {

}



